<template>
  <div>
    <v-card sx12 md12>
      <v-layout row wrap justify-center align-center >
        <v-flex xs4>
          <v-flex xs12>
            <v-btn
              outlined
              class="ma-2"
              color="success"
              @click="openSistemasFilter = true"
            >Sistemas
            <v-icon right light>fas fa-filter</v-icon>
            </v-btn>
            <v-card-text>
              <p>{{FiltroSistema}}</p>
            </v-card-text>
          </v-flex>
          <v-flex xs12>
            <v-btn
              outlined
              class="ma-2"
              color="success"
              @click="openCategoriasFilter = true"
            >
            Categorias
            <v-icon right dark>fas fa-filter</v-icon>
            </v-btn>
              <v-card-text>
              <p>{{FiltroCategoria}}</p>
            </v-card-text>
          </v-flex>
          <v-flex xs12>
            <v-btn
              outlined
              class="ma-2"
              color="success"
              @click="openColoresFilter = true"
            >
             Colores
            <v-icon right dark>fas fa-filter</v-icon>
             </v-btn>
              <v-card-text>
              <p>{{FiltroColor}}</p>
            </v-card-text>
          </v-flex>
        </v-flex>
        <v-flex xs8>
          <v-layout class="mt-5" row wrap>
            <v-flex xs4>
              <v-btn color="secondary" outlined v-on:click="setFechasTrimestres()">Trimestres</v-btn>
            </v-flex>
            <v-flex xs4>
              <v-btn color="secondary" outlined v-on:click="setFechasCuatrimestresMutation()">Cuatrimestres</v-btn>
            </v-flex>
            <v-flex xs4>
              <v-btn color="secondary" outlined v-on:click="setFechasSemestresMutation()">Semestres</v-btn>
            </v-flex>
          </v-layout>
          <v-layout row wrap>
            <v-flex xs12 md6>
              <v-card-text>
                <!-- DatePicker P1i -->
                <DatePicker
                  :fecha="PlannerModel.periodo1i"
                  :title="'P1 inicio'"
                  v-on:setDate="PlannerModel.periodo1i = $event"
                />
              </v-card-text>
            </v-flex>
            <v-flex xs12 md6>
              <v-card-text>
                <!-- DatePicker p1f -->
                <DatePicker
                  :fecha="PlannerModel.periodo1f"
                  :title="'P1 fin'"
                  v-on:setDate="PlannerModel.periodo1f = $event"
                />
              </v-card-text>
            </v-flex>
            <v-flex xs12 md6>
              <v-card-text>
                <!-- DatePicker p2i-->
                <DatePicker
                  :fecha="PlannerModel.periodo2i"
                  :title="'P2 inicio'"
                  v-on:setDate="PlannerModel.periodo2i = $event"
                />
              </v-card-text>
            </v-flex>
            <v-flex xs12 md6>
              <v-card-text>
                <!-- DatePicker p2f-->
                <DatePicker
                  :fecha="PlannerModel.periodo2f"
                  :title="'P2 fin'"
                  v-on:setDate="PlannerModel.periodo2f = $event"
                />
              </v-card-text>
            </v-flex>
            <v-flex xs12 md6>
              <v-card-text>
                <!-- DatePicker p3i -->
                <DatePicker
                  :fecha="PlannerModel.periodo3i"
                  :title="'P3 inicio'"
                  v-on:setDate="PlannerModel.periodo3i = $event"
                />
              </v-card-text>
            </v-flex>
            <v-flex xs12 md6>
              <v-card-text>
                <!-- DatePicker p3f-->
                <DatePicker
                  :fecha="PlannerModel.periodo3f"
                  :title="'P3 fin'"
                  v-on:setDate="PlannerModel.periodo3f = $event"
                />
              </v-card-text>
            </v-flex>
          </v-layout>
        </v-flex>
      </v-layout>
    </v-card>
    <TablaMultiSelect
      :openDialog="openSistemasFilter"
      :title="'Filtro Sistemas'"
      :valores="this.Sistemas"
      :headers="headers"
      v-on:setSelected="PlannerModel.FiltroSistemas = $event"
      v-on:closeDialog="openSistemasFilter = false"
    />
    <TablaMultiSelect
      :openDialog="openCategoriasFilter"
      :title="'Filtro Categorias'"
      :valores="this.Categorias"
      :headers="headers"
      v-on:setSelected="PlannerModel.FiltroCategorias = $event"
      v-on:closeDialog="openCategoriasFilter = false"
    />
    <TablaMultiSelect
      :openDialog="openColoresFilter"
      :title="'Filtro Colores'"
      :valores="this.Colores"
      :headers="headers"
      v-on:setSelected="PlannerModel.FiltroColores = $event"
      v-on:closeDialog="openColoresFilter = false"
    />
  </div>
</template>


<script>
import { mapState, mapMutations,mapActions } from "vuex";
import TablaMultiSelect from "../Shared/TablaMultiSelect.vue";
import DatePicker from "../DatePicker.vue";
import axios from "axios";

export default {
  components: {
    DatePicker,
    TablaMultiSelect
  },
  data() {
    return {
      openSistemasFilter: false,
      openCategoriasFilter: false,
      openColoresFilter: false,
      headers: [
        { text: "Codigo", value: "codigo" },
        { text: "Descripcion", value: "descripcion" }
      ],
      FiltroSistema: null,
      FiltroCategoria: null,
      FiltroColor: null
    };
  },
  computed: {
    ...mapState("SolicitudModule", ["SolicitudSelected"]),
    ...mapState("PlannerModule", [
      "PlannerModel",
      "ListaArticulosROQ",
      "Sistemas",
      "Categorias",
      "Colores"
    ])
  },
  created() {
    this.setFiltros(this.PlannerModel.proveedor);
  },
  watch: {
    "PlannerModel.FiltroSistemas"(newVal, oldVal) {
      console.log(`old: ${oldVal} new: ${newVal}`);
      this.FiltroSistema = newVal.toString();
    },
    "PlannerModel.FiltroCategorias"(newVal, oldVal) {
      console.log(`old: ${oldVal} new: ${newVal}`);
      this.FiltroCategoria = newVal.toString();
    },
    "PlannerModel.FiltroColores"(newVal, oldVal) {
      console.log(`old: ${oldVal} new: ${newVal}`);
      this.FiltroColor = newVal.toString();
    }
  },
  methods: {
    ...mapMutations("PlannerModule", [
      "setPlannerModelMutation",
      "setFechasTrimestresMutation",
      "setFechasCuatrimestresMutation",
      "setFechasSemestresMutation"
    ]),
    ...mapActions('PlannerModule',['setFiltros']),
    setPlanner() {
      this.setPlannerModelMutation(this.SolicitudSelected);
      console.log(this.PlannerModel);
    },
    async getArticulosROQ() {
      let model = this.PlannerModel;
      let url = "/Planner";
      let respuesta = await axios
        .post(url, model)
        .then(response => {
          console.log(response);
        })
        .catch(error => {
          console.log(error);
        });
      console.log(respuesta);

      this.ListaArticulosROQ = respuesta.data;
    },

    
    setFechasTrimestres() {
      this.setFechasTrimestresMutation();
      console.log(this.PlannerModel);
    },
    ConsLog() {
      console.log(this.PlannerModel);
    }
  }
};
</script>