<template>
  <div>
    <v-flex xs12>
      <v-card pl-5>
        <v-card-title>
          Solicitudes
          <v-spacer></v-spacer>
          <v-text-field
            append-icon="fas fa-search"
            v-model="search"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-card-title>
        <v-data-table
          :headers="headers"
          :items="this.articulosROQ"
          :items-per-page="10"
          :search="search"
          class="elevation-1"
          :loading="this.dataIsLoading"
          loading-text="cargando..."
        >
          <template v-slot:item.cantidadPedir="props">
            <v-edit-dialog
              :return-value.sync="props.item.cantidadPedir"
              @save="save(props.item)"
              @cancel="cancel"
              @open="open"
              @close="close"
            >
              {{ props.item.cantidadPedir }}
              <template v-slot:input>
                <v-text-field
                  v-model="props.item.cantidadPedir"
                  :rules="[validNumber]"
                  label="Edit"
                  single-line
                  counter
                ></v-text-field>
              </template>
            </v-edit-dialog>
          </template>
          <!-- <template v-slot:item.action="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)">mdi-pencil</v-icon>
          <v-icon small class="mr-2" @click="deleteItem(item)">mdi-delete</v-icon>
          </template>-->
        </v-data-table>
        <v-snackbar v-model="snack" :timeout="3000" :color="snackColor">
          {{ snackText }}
          <v-btn text @click="snack = false">Close</v-btn>
        </v-snackbar>
      </v-card>
    </v-flex>
  </div>
</template>
<script>
import { mapState } from "vuex";
import Axios from "axios";
export default {
  data() {
    return {
      search: "",
      headers: [
        // { text: "Actions", value: "action", sortable: false },
        { text: "Articulo", value: "articulo" },
        { text: "Descripcion", value: "descripcion" },
        { text: "Cantidad", value: "cantidadPedir" },
        { text: "Ventas P1", value: "ventasConsumosPeriodo1" },
        { text: "Ventas P2", value: "ventasConsumosPeriodo2" },
        { text: "Ventas P3", value: "ventasConsumosPeriodo3" },
        { text: "Existencias", value: "existencias" },
        { text: "Existencias Minima", value: "existenciasMinima" },
        { text: "ROQ1", value: "roq1" },
        { text: "ROQ2", value: "roq2" },
        { text: "ROQ3", value: "roq3" }
        // { text: "Sistema", value: "sistema" },
        // { text: "Categoria", value: "categoria" },
        // { text: "Color", value: "color" }
      ],
      sistemasSelected: ["COR P-65"],
      snack: false,
      snackColor: "",
      snackText: "",
      validNumber: v => v >= 0 || "Insert valid number!"
    };
  },
  props: ["articulosROQ", "dataIsLoading"],
  computed: {
    ...mapState("PlannerModule", ["ListaArticulosROQ", "PlannerModel"])
  },
  watch: {
    dataIsLoading(to, from) {
      console.log("to: " + to);
      console.log("from: " + from);
    }
  },
  methods: {
    async save(articulo) {
      let model = this.PlannerModel;
      let articuloSolicitud = this.SetArticuloSolicitud(articulo, model);
      console.log("Set Articulo: ");
      console.log(articuloSolicitud);
      if (articuloSolicitud.Cantidad === 0) {
        await this.DeleteArticuloSolicitud(articuloSolicitud);
      } else {
        await this.SaveArticuloSolicitud(articuloSolicitud);
      }
    },
    saveBad(articulo) {
      let artNumber = parseInt(articulo);
      if (Number.isInteger(artNumber)) {
        let model = this.$store.state.PlannerModule.PlannerModel;
        let articuloSolicitud = this.SetArticuloSolicitud(articulo, model);
        console.log("Set Articulo: ");
        console.log(articuloSolicitud);
        if (articuloSolicitud.cantidad < "0") {
          this.DeleteArticuloSolicitud(articuloSolicitud);
        } else {
          this.SaveArticuloSolicitud(articuloSolicitud);
        }
        this.snack = true;
        this.snackColor = "success";
        this.snackText = "Data saved";
      } else {
        this.snack = true;
        this.snackColor = "error";
        this.snackText = "Data error";
      }
    },
    async SaveArticuloSolicitud(articuloSolicitud) {
      let url = "/SolicitudCompraLineas";
      console.log(articuloSolicitud);
      let respuesta = await Axios.post(url, articuloSolicitud)
        .then(response => {
          console.log(response);
          this.snack = true;
          this.snackColor = "success";
          this.snackText = "Datos guardados correctamente";
        })
        .catch(error => {
          console.log(error);
        });

      return respuesta;
    },
    async DeleteArticuloSolicitud(articulo) {
      let url = `/SolicitudCompraLineas/${articulo.SolicitudId}?articulo=${articulo.ArticuloId}`;
      console.log(url);
      let respuesta = await Axios.delete(url)
        .then(response => {
          console.log(response);
          this.snack = true;
          this.snackColor = "success";
          this.snackText = "Articulo eliminado de la solicitud";
        })
        .catch(error => {
          console.log(error);
        });

      return respuesta;
    },
    SetArticuloSolicitud(articulo, model) {
      let articuloSolicitud = {
        SolicitudId: model.solicitudId,
        ArticuloId: articulo.articulo,
        Cantidad: parseInt(articulo.cantidadPedir)
      };
      return articuloSolicitud;
    },
    cancel() {
      this.snack = true;
      this.snackColor = "error";
      this.snackText = "Canceled";
    },
    open() {
      this.snack = true;
      this.snackColor = "info";
      this.snackText = "Dialog opened";
    },
    close() {
      console.log("Dialog closed");
    }
  }
};
</script>