<template>
  <div class="m-5">
    <v-card pl-5>
      <v-card-title>
        Solicitudes
        <v-spacer></v-spacer>
        <v-btn color="success" text @click="createSolicitud">
          <v-icon color="green" Outline>mdi-plus-circle</v-icon>Add
        </v-btn>
        <!-- <v-btn color="success" text v-on:click="update">Update</v-btn> -->
        <v-spacer></v-spacer>
        <v-text-field
          append-icon="mdi-search"
          v-model="search"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </v-card-title>
      <v-data-table
        :sort-by="['id']"
        :sort-desc="['true']"
        :headers="headers"
        :items="this.Solicitudes"
        :items-per-page="10"
        :search="search"
        class="elevation-1"
      >
        <template v-slot:item.action="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)">mdi-pencil</v-icon>
          <v-icon small class="mr-2" @click="deleteItem(item)">mdi-delete</v-icon>
        </template>
      </v-data-table>
    </v-card>
  </div>
</template>
<script>
import axios from "axios";
import { mapActions, mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      dialog: false,
      search: "",
      headers: [
        { text: "Actions", value: "action", sortable: false },
        { text: "Solicitud", value: "id"},
        { text: "Nombre Proveedor", value: "nombreProveedor" },
        { text: "Referencia", value: "referencia" },
        { text: "Estado", value: "estado" },
        { text: "Fecha", value: "fecha" }
      ],
      solicitudesDummy: [
        {
          solicitud: "SC00001",
          referencia: null,
          proveedor: "P0002",
          nombreProveedor: "JIANGY PERFILES -CHINA ALUMINUM",
          fecha: "2019-10-18T00:00:00",
          estado: "En proceso",
          descripcion: null,
          eliminada: null,
          paqueteConteo: null,
          plazoAbastecimiento: 60,
          fechaModificado: "2019-10-18T13:50:22.1648826",
          fechaColocacion: null,
          fechaEstimadaRecepcion: null,
          solicitudCompraLineas: null
        }
      ]
    };
  },
  created() {
    this.update();
  },
  computed: {
    ...mapState("SolicitudModule", ["NuevaSolicitud", "Solicitudes"]),
   
  },
  methods: {
    ...mapActions("SolicitudModule", ["updateSolicitud", "updateSolicitudes"]),
    ...mapMutations("SolicitudModule", ["newSolicitudMutation"]),

    update() {
      let url = "/SolicitudCompras";
      axios.get(url).then(response => {
        this.updateSolicitudes(response.data);
      }).catch(err=>console.log(err))
    },
    createSolicitud() {
      this.$router.push(`/editarSolicitud/nueva`);
    },

    editItem(item) {
      console.log(item);
      // this.updateSolicitud(item);
      this.$router.push(`/editarSolicitud/${item.id}`);
    },

    async deleteItem(item) {
      let url = `/SolicitudCompras/${item.id}`;
      console.log(url);
      const index = this.Solicitudes.indexOf(item);
      let deleteConfirmed = confirm(
        "Are you sure you want to delete this item?"
      );
      if (deleteConfirmed) {
        this.Solicitudes.splice(index, 1);
        let response = await axios.delete(url);
        console.log(response);
      }
    }
  }
};
</script>