<template>
  <div>
    <!-- <v-card>
      <v-card-title primary-title>Planner</v-card-title>
      <v-card-title primary-title>{{PlannerModel.solicitud}}</v-card-title>
    </v-card>-->
    <v-btn  large  color="success" @click="getArticulosROQ">GetArticulos <v-icon>mdi-cached</v-icon></v-btn>

    <v-layout row wrap>
      <v-flex xs12>
        <FiltrosPlanner />
      </v-flex>
    </v-layout>
    <v-layout row wrap>
      <v-flex xs12></v-flex>
      <TablaPlanner :articulosROQ="this.ListaArticulosROQ"  :dataIsLoading="this.dataIsLoading" />
    </v-layout>
  </div>
</template>
<script>
import FiltrosPlanner from "./FiltrosPlanner";
import TablaPlanner from "./TablaPlanner";
import { mapState, mapMutations } from "vuex";
import axios from "axios";
export default {
  data() {
    return {
      articulos: [],
      dataIsLoading: false
    };
  },
  components: {
    TablaPlanner,
    FiltrosPlanner
  },
  computed: {
    ...mapState("PlannerModule", [
      "PlannerModel",
      "ListaArticulosROQ",
      "Sistemas",
      "Categorias",
      "Colores"
    ]),
    ...mapState("SolicitudModule", ["SolicitudSelected"]),
  },

  methods: {
    ...mapMutations("PlannerModule", [
      "setPlannerModelMutation",
      "setReponseMutation",
      'setListaArticulosROQ'
    ]),
    ConsoleLog() {
      console.log(this.PlannerModel);
    },

    async getArticulosROQ() {
      this.dataIsLoading = true
      let url = "/ComprasPlanner";
      let data = this.PlannerModel;
      await axios
        .post(url, data)
        .then(response => {
          this.setListaArticulosROQ(response);
          console.log(response)
        })
        .catch(error => {
          console.log(error);
          this.dataIsLoading = false;
        }).finally(()=>{
          this.dataIsLoading = false;
        })
        
  
    }
  },
  created() {
    this.setPlannerModelMutation(this.SolicitudSelected);
  }
};
</script>