import moment from "moment"
import axios from 'axios'

export default {

    namespaced: true,

    state: {

        ListaArticulosROQ: [],
        Sistemas: [],
        Categorias: [],
        Colores: [],

        PlannerModel: {
            solicitudId: 'nueva',
            proveedor: 'Proveedor',
            paqueteConteo: null,
            plazoAbastecimiento: 60,
            periodo1i: moment(new Date()).format('YYYY-MM-DD'),
            periodo1f: moment(new Date()).format('YYYY-MM-DD'),
            periodo2i: moment(new Date()).format('YYYY-MM-DD'),
            periodo2f: moment(new Date()).format('YYYY-MM-DD'),
            periodo3i: moment(new Date()).format('YYYY-MM-DD'),
            periodo3f: moment(new Date()).format('YYYY-MM-DD'),
            FiltroSistemas: [],
            FiltroCategorias: [],
            FiltroColores: [],

        }
    },

    mutations: {
        setPlannerModelMutation(state, solicitud) {

            state.PlannerModel.solicitudId = solicitud.id;
            state.PlannerModel.proveedor = solicitud.proveedor;
            state.PlannerModel.paqueteConteo = solicitud.paqueteConteo;
            state.PlannerModel.plazoAbastecimiento = solicitud.plazoAbastecimiento;
            state.PlannerModel.periodo1i = moment(new Date()).format('YYYY-MM-DD');
            state.PlannerModel.periodo1f = moment(new Date()).format('YYYY-MM-DD');
            state.PlannerModel.periodo2i = moment(new Date()).format('YYYY-MM-DD');
            state.PlannerModel.periodo2f = moment(new Date()).format('YYYY-MM-DD');
            state.PlannerModel.periodo3i = moment(new Date()).format('YYYY-MM-DD');
            state.PlannerModel.periodo3f = moment(new Date()).format('YYYY-MM-DD');

        },
        setFechasTrimestresMutation(state) {
            state.PlannerModel.periodo1f = moment(new Date()).format("YYYY-MM-DD");
            state.PlannerModel.periodo1i = moment(new Date()).subtract(3, "months").format("YYYY-MM-DD");
            state.PlannerModel.periodo2f = moment(new Date())
                .subtract(3, "months")
                .format("YYYY-MM-DD");
            state.PlannerModel.periodo2i = moment(new Date())
                .subtract(6, "months")
                .format("YYYY-MM-DD");
            state.PlannerModel.periodo3f = moment(new Date())
                .subtract(6, "months")
                .format("YYYY-MM-DD");
            state.PlannerModel.periodo3i = moment(new Date())
                .subtract(9, "months")
                .format("YYYY-MM-DD");
        },
        setFechasSemestresMutation(state) {
            state.PlannerModel.periodo1f = moment(new Date()).format("YYYY-MM-DD");
            state.PlannerModel.periodo1i = moment(new Date()).subtract(6, "months").format("YYYY-MM-DD");
            state.PlannerModel.periodo2f = moment(new Date())
                .subtract(6, "months")
                .format("YYYY-MM-DD");
            state.PlannerModel.periodo2i = moment(new Date())
                .subtract(12, "months")
                .format("YYYY-MM-DD");
            state.PlannerModel.periodo3f = moment(new Date())
                .subtract(12, "months")
                .format("YYYY-MM-DD");
            state.PlannerModel.periodo3i = moment(new Date())
                .subtract(18, "months")
                .format("YYYY-MM-DD");
        },
        setFechasCuatrimestresMutation(state) {
            state.PlannerModel.periodo1f = moment(new Date()).format("YYYY-MM-DD");
            state.PlannerModel.periodo1i = moment(new Date()).subtract(4, "months").format("YYYY-MM-DD");
            state.PlannerModel.periodo2f = moment(new Date())
                .subtract(4, "months")
                .format("YYYY-MM-DD");
            state.PlannerModel.periodo2i = moment(new Date())
                .subtract(8, "months")
                .format("YYYY-MM-DD");
            state.PlannerModel.periodo3f = moment(new Date())
                .subtract(8, "months")
                .format("YYYY-MM-DD");
            state.PlannerModel.periodo3i = moment(new Date())
                .subtract(12, "months")
                .format("YYYY-MM-DD");
        },

        setReponseMutation(state, data) {
            state.ListaArticulosROQ = data.listaArticulos
            state.Sistemas = data.sistemas
            state.Categorias = data.categorias
            state.Colores = data.colores
        },
        setListaArticulosROQ(state, response) {
            console.log('Desde Store')
            console.log(response.data)
            state.ListaArticulosROQ = response.data
        },

        setFiltrosMutation(state, filtros) {
            state.Sistemas = filtros.data.sistemas
            state.Categorias = filtros.data.categorias
            state.Colores = filtros.data.colores
        },
        resetPlanner(state) {
            state.ListaArticulosROQ = [],
             state.PlannerModel = {
                    solicitudId: 'nueva',
                    proveedor: 'Proveedor',
                    paqueteConteo: null,
                    plazoAbastecimiento: 60,
                    periodo1i: moment(new Date()).format('YYYY-MM-DD'),
                    periodo1f: moment(new Date()).format('YYYY-MM-DD'),
                    periodo2i: moment(new Date()).format('YYYY-MM-DD'),
                    periodo2f: moment(new Date()).format('YYYY-MM-DD'),
                    periodo3i: moment(new Date()).format('YYYY-MM-DD'),
                    periodo3f: moment(new Date()).format('YYYY-MM-DD'),
                    FiltroSistemas: [],
                    FiltroCategorias: [],
                    FiltroColores: [],
                }

        }


    },
    actions: {
        async setFiltros({ commit }, proveedor) {
            let url = `/ComprasPlanner/filtros?proveedor=${proveedor}`
            await axios.get(url).then(resp => {
                console.log(resp)
                commit('setFiltrosMutation', resp)
            }).catch(err => console.log(err))
        }


    }




}