<template>
  <v-row justify="center">
    <v-dialog v-model="this.openDialog" persistent max-width="600px">
      <v-card>
        <v-card-title>
          <span class="headline">Proveedores</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-card-title>
                <v-spacer></v-spacer>
                <v-text-field
                  append-icon="fas fa-search"
                  v-model="search"
                  label="Search"
                  single-line
                  hide-details
                ></v-text-field>
              </v-card-title>
              <v-data-table
                :headers="headers"
                :items="proveedores"
                :items-per-page="10"
                :search="search"
                class="elevation-1"
              >
                <template v-slot:item.action="{ item }">
                  <v-icon small class="mr-2" @click="setProveedor(item)">fas fa-check-circle</v-icon>
                </template>
              </v-data-table>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="blue darken-1"
            text
            @click="closeDialog()"
          >Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>
<script>
import axios from "axios";
import { mapActions, mapState } from "vuex";
export default {
  data: () => ({
    search: "",
    proveedores: [],
    headers: [
      { text: "Codigo", value: "proveedor1" },
      { text: "Nombre Proveedor", value: "nombre" },
      { text: "Actions", value: "action", sortable: false }
    ],
    
    
  }),
  props: {
    openDialog: Boolean
  },
  computed: {
    ...mapState(["SolicitudSelected", "openProveedoresSearch"])
  },
  created() {
    this.getProveedores();
  },
  methods: {
    ...mapActions(["updateProveedor"]),

    setProveedor(proveedor){
      this.$emit('setProveedor',proveedor)
      this.closeDialog()

    },
    closeDialog(){
      this.$emit('closeDialog')
      console.log('Cerrar')

    },

    async getProveedores() {
      let url = "/SolicitudCompras/proveedores";
      let respuesta = await axios.get(url);
      console.log(respuesta);
      this.proveedores = respuesta.data;
    },
    selectProveedor(proveedor) {
      this.updateProveedor(proveedor);
      this.$store.state.openProveedoresSearch = false;
    },
    getSolicitudSelected() {
      console.log(this.SolicitudSelected);
    }
  }
};
</script>