<template>
  <div>
    <v-card class="pa-5">
      <form>
        <v-text-field v-model="SolicitudSelected.referencia" label="Referencia"></v-text-field>
        <v-text-field
          v-if="this.nuevaSolicitud"
          v-model="SolicitudSelected.proveedor"
          :error-messages="requiredErrors($v.SolicitudSelected.proveedor)"
          label="Proveedor"
          append-icon="fas fa-search"
          @click:append="openProveedoresDialog=true"
          required
          @input="$v.SolicitudSelected.proveedor.$touch()"
          @blur="$v.SolicitudSelected.proveedor.$touch()"
        ></v-text-field>
        <!-- <p>{{this.$v.SolicitudSelected.proveedor}}</p> -->
        <v-text-field
          v-if="this.nuevaSolicitud"
          v-model="SolicitudSelected.nombreProveedor"
          :error-messages="requiredErrors($v.SolicitudSelected.nombreProveedor)"
          label="Nombre Proveedor"
          required
          @input="$v.SolicitudSelected.nombreProveedor.$touch()"
          @blur="$v.SolicitudSelected.nombreProveedor.$touch()"
        ></v-text-field>
        <!-- <p>{{this.$v.SolicitudSelected.nombreProveedor}}</p> -->

        <v-text-field
          v-model.number="SolicitudSelected.plazoAbastecimiento"
          :error-messages="requiredErrors($v.SolicitudSelected.plazoAbastecimiento)"
          label="Plazo Abastecimiento"
          required
          @input="$v.SolicitudSelected.plazoAbastecimiento.$touch()"
          @blur="$v.SolicitudSelected.plazoAbastecimiento.$touch()"
        ></v-text-field>
        <v-select
          :items="estados"
          v-model="SolicitudSelected.estado"
          :error-messages="requiredErrors($v.SolicitudSelected.estado)"
          label="Estados"
          @input="$v.SolicitudSelected.estado.$touch()"
          @blur="$v.SolicitudSelected.estado.$touch()"
        ></v-select>
        <!-- DatePicker -->
        <DatePicker :fecha="SolicitudSelected.fecha" :title="fecha" v-on:setDate="SolicitudSelected.fecha = $event" />
        <!-- <v-btn color="success" @click="ConsoleLog(testDate)">Fecha</v-btn> -->
        <!-- <v-datetime-picker label="Fecha" v-model="SolicitudSelected.fecha"> </v-datetime-picker> -->

        <v-btn class="btn btn-primary" @click="submit">submit</v-btn>
      </form>
    </v-card>
    <h2></h2>
      <TablaProveedores :openDialog="this.openProveedoresDialog" v-on:setProveedor="SolicitudSelected.proveedor = $event.proveedor1, SolicitudSelected.nombreProveedor = $event.nombre" v-on:closeDialog="openProveedoresDialog=false" />



  </div>
</template>
<script>
import moment from 'moment'
import TablaProveedores from "../components/TablaProveedores.vue";
import { validationMixin } from "vuelidate";
import { required } from "vuelidate/lib/validators";
import { mapActions, mapState, mapMutations,mapGetters } from "vuex";
import DatePicker from "../components/DatePicker.vue";

import axios from "axios";
export default {
  props: ["id"],
  data: () => ({
    openProveedoresDialog: false,
    SolicitudEntity: {},
    estados: ["Proceso", "Colocada", "Descartada"],
    fecha: "Fecha ",
    fechaTestLabel: "Fecha Test",
    menu2: false,
    testDate: moment(new Date()).format('YYYY-MM-DD')
  }),
  components: {
    DatePicker,
    TablaProveedores,

  },
  mixins: [validationMixin],

  validations: {
    SolicitudSelected: {
      nombreProveedor: { required },
      proveedor: { required },
      estado: { required },
      plazoAbastecimiento: { required }
    }
  },
  created() {
    console.log("Form Created");
    console.log("FormID " + this.id);
    console.log(this.SolicitudSelected);
    if (this.id === "nueva") {
      console.log("Es nueva");
      this.newSolicitudMutation();
    } else {
      this.getSolicitud(this.id);
    }
  },
  beforeUpdate() {
    console.log("Form beforeUpdate");
  },
  updated() {
    console.log("Form Updated");
  },
  destroyed() {
    console.log("Form Destroyed");
  },

  computed: {
    ...mapState("SolicitudModule", [
      "SolicitudSelected",
      "openProveedoresSearch",
      "validators",
      "nuevaSolicitud"
    ]),
      ...mapGetters("SolicitudModule", [
      "nuevaSolicitud"
    ]),
    solicitudErrors() {
      let errors = [];
      if (!this.$v.solicitud.$dirty) return errors;
      !this.$v.solicitud.required && errors.push("Inserte un registro vlido");
      return errors;
    },
    fechaSolicitud() {
      return this.$store.state.SolicitudSelected.fecha;
    }
  },
  methods: {
    ConsoleLog(e){
      console.log(e)
    },
    ...mapActions("SolicitudModule", ["updateSolicitud"]),
    ...mapMutations("SolicitudModule", ["newSolicitudMutation"]),
    getEvent(e) {
      console.log(e);
    },
    solicitudObjeto() {
      this.solicitudModel = {
        solicitud: this.solicitud,
        proveedor: this.proveedor,
        nombreProveedor: this.nombreProveedor,
        fecha: this.fecha,
        fechaModificado: this.fechaModificado,
        estado: this.estado,
        plazoAbastecimiento: this.plazoAbastecimiento
      };
      return this.solicitudModel;
    },
    async submit() {
      console.log(this.SolicitudSelected);

      this.$v.$touch();
      if (this.$v.$invalid == false) {
        console.log("Form valido");
        if (this.SolicitudSelected.id === undefined) {
          await this.postSolicitud(this.SolicitudSelected);
        } else {
          await this.putSolicitud(this.SolicitudSelected);
        }
      } else {
        console.log("Form invalido");
        console.log(this.$v.$invalid);
        console.log(this.SolicitudSelected);
      }
    },
    requiredErrors(validator) {
      let errors = [];
      if (!validator.$dirty) return errors;
      !validator.required && errors.push("Inserte un registro vlido");
      return errors;
    },
    async getSolicitud(solicitud) {
      let url = `/SolicitudCompras/${solicitud}`;
      let respuesta = await axios
        .get(url)
        .then(response => {
          this.updateSolicitud(response.data);
        })
        .catch(error => {
          console.log(error);
          this.$router.push(`/editarSolicitud/nueva`);
        });
      console.log(respuesta);
      // if (respuesta.status === 200) {
      //   console.log(respuesta.data);
      //   this.updateSolicitud(respuesta.data);
      // }
    },

    async postSolicitud(modelo) {
      console.log(modelo);
      var url = "/SolicitudCompras";
       await axios.post(url, modelo)
      .then(resp=>{
        this.updateSolicitud(resp.data);
        this.$router.push(`/editarSolicitud/${resp.data.id}`);
        console.log(resp)
        }).catch(err=>console.log(err))
      
    },
    async putSolicitud(modelo) {
      console.log(modelo);
      var url = `/SolicitudCompras/${modelo.id}`;
      let data = await axios.put(url, modelo);
      console.log(data);
    },
    openProveedoresModal() {
      // console.log("test");
      // console.log(this.openProveedoresSearch);
      // this.$store.state.openProveedoresSearch = true;
      this.openProveedoresDialog = true
    }
  }
};
</script>