<template>
  <div>
    <div>
      <v-layout row wrap justify-center>
        <v-flex xs12 md10>
          <v-card>
            <v-card-title primary-title>
              <h1 v-if="this.nuevaSolicitud">Nueva Solicitud</h1>
              <h2>{{this.SolicitudSelected.id}}</h2>
            </v-card-title>
            <v-card-text v-if=" this.nuevaSolicitud === false">
              <h1>{{this.SolicitudSelected.proveedor}} {{this.SolicitudSelected.nombreProveedor}}</h1>
            </v-card-text>
            <v-card-text v-if="this.nuevaSolicitud===false">
              <h1>Referencia: {{this.SolicitudSelected.referencia}}</h1>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
      <!-- Tab -->
      <v-layout row wrap justify-center>
        <v-flex xs12 md10>
          <v-tabs v-model="tab">
            <v-tab>Datos</v-tab>
            <v-tab v-if="!nuevaSolicitud" @click="this.getLineasSolicitud">Solicitud</v-tab>
            <v-tab v-if="!this.nuevaSolicitud" @click="setFiltros">Planner</v-tab>
          </v-tabs>
          <v-tabs-items v-model="tab" class="pa-5">
            <v-tab-item>
              <!-- Datos -->
              <v-layout row wrap>
                <v-flex sx12>
                  <FormSolicitudCompra :id="this.id" />
                </v-flex>
              </v-layout>
            </v-tab-item>
            <v-tab-item>
              <!-- Contenido -->
              <v-layout row wrap>
                <v-flex sx12>
                  <TablaLineasSolicitud />
                </v-flex>
              </v-layout>
            </v-tab-item>
            <v-tab-item>
              <!-- Contenido -->
              <v-layout row wrap>
                <v-flex sx12>
                  <Planner />
                </v-flex>
              </v-layout>
            </v-tab-item>
          </v-tabs-items>
        </v-flex>
      </v-layout>
    </div>
  </div>
</template>

<script>
import Planner from "../components/Planner/Planner.vue";
import FormSolicitudCompra from "../components/FormSolicitudCompra.vue";
import TablaLineasSolicitud from "../components/Planner/TablaLineasSolicitud.vue";

import { mapState, mapGetters, mapMutations, mapActions } from "vuex";

export default {
  components: {
    FormSolicitudCompra,
    Planner,
    TablaLineasSolicitud
  },
  data() {
    return {
      id: this.$route.params.id,
      tab: 0,
      items: ["Datos", "Solicitud", "Planner"]
    };
  },
  computed: {
    ...mapState("SolicitudModule", ["SolicitudSelected"]),
    ...mapGetters("SolicitudModule", ["nuevaSolicitud"]),
    ...mapState(["test"]),
    esNueva() {
      return this.$store.SolicitudModule.getters.nuevaSolicitud;
    }
  },
  methods: {
    ...mapMutations("PlannerModule", ["resetPlanner"]),
    ...mapActions("SolicitudModule", ["getLineasSolicitud"]),
    ...mapActions("PlannerModule", ["setFiltros"]),
    log(e) {
      console.log(e);
    }
  },
  watch: {
    $route(to) {
      this.id = to.params.id;
      console.log("Desde watch" + this.id);
    }
  },
  created() {
    console.log("Editar Solicitud Created");
    console.log("eS => id: " + this.id);
    this.$store.state.SolicitudModule.LineasSolicitudSelected = [];
  },
  updated() {
    console.log("Editar Solicitud Updated");
    console.log(this.id);
  },
  destroyed() {
    console.log("Editar Solicitud Destroyed");
    this.resetPlanner();
  }
};
</script>
