
<template>
  <div>
    <v-menu
      v-model="menu2"
      :close-on-content-click="false"
      :nudge-right="40"
      transition="scale-transition"
      offset-y
      min-width="290px"
    >
      <template v-slot:activator="{ on }">
        <v-text-field
          v-model="fechaLocal"
          :label="title"
          prepend-icon="fas fa-calendar"
          readonly
          v-on="on"
        ></v-text-field>
      </template>
      <v-date-picker v-model="fechaLocal" @input="this.setFecha"></v-date-picker>
    </v-menu>
  </div>
</template>
<script>
import moment from "moment";

export default {
  data: () => ({
    menu2: false,
    fechaLocal: moment(new Date()).format("YYYY-MM-DD")
  }),
  props: ["fecha", "title"],

  methods: {
    setFecha() {
      this.$emit("setDate", this.fechaLocal);
      this.menu2 = false;
    }
  },
  watch: {
    fecha(newValue,oldValue) {
      this.fechaLocal = newValue
      console.log(`old: ${oldValue} new: ${newValue}`);
    }
  }
};
</script>