<template>
  <div>
    <v-row justify="center">
      <v-dialog v-model="this.openDialog" persistent max-width="600px">
        <v-card>
          <v-card-title>
            <h1>{{this.title}}</h1>
            <span class="headline"></span>
          </v-card-title>
          <v-card-text>
            <v-data-table
              v-model="selected"
              :headers="headers"
              :items="valores"
              :single-select="singleSelect"
              item-key="descripcion"
              show-select
              class="elevation-1"
            >
              <template v-slot:top>
                <v-switch v-model="singleSelect" label="Single select" class="pa-3"></v-switch>
              </template>
            </v-data-table>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="closeDialog">Close</v-btn>
            <!-- <v-btn color="success" @click="checkValores()">CheckValores</v-btn> -->
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </div>
</template>
<script>
export default {
  data() {
    return {
      singleSelect: false,
      selected: [],
      selectedstring: []
    };
  },
  watch: {
    selected(newVal, oldVal) {
      this.selectedstring = [];
      newVal.forEach(element => {
        this.selectedstring.push(element.descripcion);
      });

      this.$emit("setSelected", this.selectedstring);

      console.log(`old: ${oldVal} new: ${newVal}`);
    }
  },
  props: ["title", "openDialog", "headers", "valores"],
  methods: {
    closeDialog() {
      console.log(this.selected);
      this.$emit("closeDialog");
    },
    checkValores() {
      console.log("Checkeando valores");
      console.log(this.valores.length);
      this.valores.forEach(element => {
        console.log(element);
      });
      console.log("Checkeando selected");
      this.selectedstring.forEach(element => {
        console.log(element);
      });
    }
  }
};
</script>