<template>
  <div>
    <v-card>
      <v-toolbar color="light" class="mb-5">
        <v-toolbar-title>Articulos Solicitud</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-text-field
          v-model="search"
          append-icon="fas fa-search"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
        <v-spacer></v-spacer>
        <v-toolbar-items>
          <v-card-title primary-title></v-card-title>
          <v-switch v-model="activarPesos" label="Activar PesoKG" class="pa-3"></v-switch>

          <v-btn text @click="this.exportExcel">
            <v-icon dark left>fas fa-file-download</v-icon>xlsx
          </v-btn>
          <v-card-title text v-if="activarPesos">Kg: {{this.pesoTotal}}</v-card-title>
        </v-toolbar-items>
      </v-toolbar>
      <v-data-table
        :headers="tipoHeader"
        :items="this.LineasSolicitudSelected"
        class="elevation-1"
        :search="search"
      >
        <template v-slot:item.action="{ item }">
          <v-icon small class="mr-2" @click="deteleLinea(item)">mdi-delete</v-icon>
        </template>
        <template v-slot:item.cantidad="props">
          <v-edit-dialog
            :return-value.sync="props.item.cantidad"
            @save="save(props.item)"
            @cancel="cancel"
            @open="open"
            @close="close"
          >
            {{ props.item.cantidad }}
            <template v-slot:input>
              <v-text-field
                v-model="props.item.cantidad"
                :rules="[validNumber]"
                label="Edit"
                single-line
                counter
              ></v-text-field>
            </template>
          </v-edit-dialog>
        </template>
      </v-data-table>
      <v-snackbar v-model="snack" :timeout="3000" :color="snackColor">
        {{ snackText }}
        <v-btn text @click="snack = false">Close</v-btn>
      </v-snackbar>
    </v-card>
  </div>
</template>
<script>
import { mapActions, mapState, mapGetters } from "vuex";
import XLSX from "xlsx";
import axios from "axios";
export default {
  data() {
    return {
      snack: false,
      snackColor: "",
      snackText: "",
      validNumber: v => v >= 0 || "Insert valid number!",
      activarPesos: false,
      search: "",
      headers: [
        // { text: "Solicitud", value: "solicitudId" },
        { text: "Actions", value: "action", sortable: false },
        { text: "Articulo", value: "articuloId" },
        { text: "Descripcion", value: "descripcion" },
        { text: "Cantidad", value: "cantidad" },
        { text: "Descripcion Suplidor", value: "descripCatalogo" },
        { text: "Codigo Suplidor", value: "codigoCatalogo" }
      ],
      headersPeso: [
        // { text: "Solicitud", value: "solicitudId" },
        { text: "Actions", value: "action", sortable: false },
        { text: "Articulo", value: "articuloId" },
        { text: "Descripcion", value: "descripcion" },
        { text: "Cantidad", value: "cantidad" },
        { text: "Descripcion Suplidor", value: "descripCatalogo" },
        { text: "Codigo Suplidor", value: "codigoCatalogo" },
        { text: "Peso KgM", value: "pesoKg" },
        { text: "Peso Total", value: "pesoTotal" }
      ]
    };
  },
  computed: {
    ...mapState("SolicitudModule", [
      "LineasSolicitudSelected",
      "SolicitudSelected"
    ]),
    ...mapGetters("SolicitudModule", ["pesoTotal"]),
    ...mapState("PlannerModule", ["PlannerModel"]),
    tipoHeader() {
      if (this.activarPesos) {
        console.log(this.headersPeso);
        console.log("SetTipoHeader");
        return this.headersPeso;
      } else {
        console.log("SetTipoHeader");
        console.log(this.headers);
        return this.headers;
      }
    }
  },
  watch: {
    activarPesos(to) {
      console.log(to);
      console.log(this.tipoHeader);
    }
  },
  methods: {
    ...mapActions("SolicitudModule", ["getLineasSolicitud"]),

    exportExcel: function() {
      let data = XLSX.utils.json_to_sheet(this.LineasSolicitudSelected);
      const workbook = XLSX.utils.book_new();
      const filename = `SolicitudCompra`;
      XLSX.utils.book_append_sheet(workbook, data, filename);
      XLSX.writeFile(workbook, `${filename}.xlsx`);
    },
    async save(articulo) {
      let model = this.PlannerModel;
      let articuloSolicitud = this.SetArticuloSolicitud(articulo, model);
      console.log("Set Articulo: ");
      console.log(articuloSolicitud);
      if (articuloSolicitud.Cantidad === 0) {
        await this.DeleteArticuloSolicitud(articuloSolicitud);
      } else {
        await this.SaveArticuloSolicitud(articuloSolicitud);
      }
    },
    SetArticuloSolicitud(articulo, model) {
      let articuloSolicitud = {
        SolicitudId: model.solicitudId,
        ArticuloId: articulo.articuloId,
        Cantidad: parseInt(articulo.cantidad)
      };
      return articuloSolicitud;
    },
     async SaveArticuloSolicitud(articuloSolicitud) {
      let url = "/SolicitudCompraLineas";
      console.log(articuloSolicitud);
      let respuesta = await axios.post(url, articuloSolicitud)
        .then(response => {
          console.log(response);
          this.snack = true;
          this.snackColor = "success";
          this.snackText = "Datos guardados correctamente";
        })
        .catch(error => {
          console.log(error);
        });

      return respuesta;
    },
    async DeleteArticuloSolicitud(articulo) {
      let url = `/SolicitudCompraLineas/${articulo.SolicitudId}?articulo=${articulo.ArticuloId}`;
      console.log(url);
      let respuesta = await axios.delete(url)
        .then(response => {
          console.log(response);
          this.snack = true;
          this.snackColor = "success";
          this.snackText = "Articulo eliminado de la solicitud";
        })
        .catch(error => {
          console.log(error);
        });

      return respuesta;
    },

    deteleLinea: async function(item) {
      console.log(item.articuloId);
      console.log(this.SolicitudSelected.id);
      let url = `/SolicitudCompraLineas/${this.SolicitudSelected.id}?articulo=${item.articuloId}`;
      console.log(url);
      const index = this.LineasSolicitudSelected.indexOf(item);
      let deleteConfirmed = confirm(
        "Are you sure you want to delete this item?"
      );
      if (deleteConfirmed) {
        this.LineasSolicitudSelected.splice(index, 1);
        let response = await axios.delete(url).then(response => {
          console.log(response);
          this.snack = true;
          this.snackColor = "success";
          this.snackText = "Articulo eliminado de la solicitud";
        })
        .catch(error => {
          console.log(error);
        });
        console.log(response);
      }
    },
    cancel() {
      this.snack = true;
      this.snackColor = "error";
      this.snackText = "Canceled";
    },
    open() {
      this.snack = true;
      this.snackColor = "info";
      this.snackText = "Dialog opened";
    },
    close() {
      console.log("Dialog closed");
    }
  },
  created() {},
  mounted() {
    console.log("Tabla lineas montada");
  }
};
</script>
