/* eslint-disable prettier/prettier */
import moment from "moment"
import axios from 'axios'


const state = {
    cuenta: 0,
    openProveedoresSearch: false,
    Nombre: 'Juan',
    Solicitudes: [],
    SolicitudSelected: {},
    LineasSolicitudSelected: []
}

const getters = {
    nuevaSolicitud(state) {
        return state.SolicitudSelected.id === undefined
    },
    pesoTotal(state){
        console.log('Exec PesoTotal')
        let count = 0
        if (state.LineasSolicitudSelected.length > 0 ) {
            console.log('Hay registros')
            state.LineasSolicitudSelected.forEach(element => {
                count = count + element.pesoTotal
            });
            
        }
        console.log('Total: '+count)
            return count;
        
       
    }


}

const mutations = {
    increment(state) {
        state.cuenta++;
    },
    newSolicitudMutation(state) {
        state.SolicitudSelected = {
            referencia: null,
            nombreProveedor: null,
            estado: null,
            descripcion: null,
            eliminada: null,
            paqueteConteo: null,
            plazoAbastecimiento: 60,
            fecha: moment(new Date()).format('YYYY-MM-DD'),
            fechaModificado: moment(new Date()).format('YYYY-MM-DD'),
            fechaColocacion: null,
            fechaEstimadaRecepcion: null,
        },
        state.LineasSolicitudSelected = []
    },
    UpdateSolicitudMutation(state, solicitud) {
        state.SolicitudSelected = {
            id: solicitud.id,
            referencia: solicitud.referencia,
            proveedor: solicitud.proveedor,
            nombreProveedor: solicitud.nombreProveedor,
            estado: solicitud.estado,
            descripcion: solicitud.descripcion,
            plazoAbastecimiento: solicitud.plazoAbastecimiento,
            paqueteConteo: solicitud.paqueteConteo,
            fecha: moment(solicitud.fecha).format('YYYY-MM-DD'),
            fechaModificado: moment(solicitud.fechaModificado).format('YYYY-MM-DD'),
        }
        state.SolicitudSelected.fechaColocacion = solicitud.fechaColocacion !== null ? moment(solicitud.fechaColocacion).format('YYYY-MM-DD') : null;
        state.SolicitudSelected.fechaEstimadaRecepcion = solicitud.fechaEstimadaRecepcion !== null ? moment(solicitud.fechaEstimadaRecepcion).format('YYYY-MM-DD') : null;
    },
    updateProveedorMutation(state, proveedorModel) {
        state.SolicitudSelected.proveedor = proveedorModel.proveedor1;
        state.SolicitudSelected.nombreProveedor = proveedorModel.nombre;


    },
    updateSolicitudesMutation(state, SolicitudesUpdated) {
        state.Solicitudes = [];

        for (let i = 0; i < SolicitudesUpdated.length; i++) {
            let element = {
                id: SolicitudesUpdated[i].id,
                referencia: SolicitudesUpdated[i].referencia,
                proveedor: SolicitudesUpdated[i].proveedor,
                nombreProveedor: SolicitudesUpdated[i].nombreProveedor,
                estado: SolicitudesUpdated[i].estado,
                descripcion: SolicitudesUpdated[i].descripcion,
                plazoAbastecimiento: SolicitudesUpdated[i].plazoAbastecimiento,
                paqueteConteo: SolicitudesUpdated[i].paqueteConteo,
                fecha: moment(SolicitudesUpdated[i].fecha).format('YYYY-MM-DD'),
                fechaModificado: moment(SolicitudesUpdated[i].fechaModificado).format('YYYY-MM-DD'),
                fechaEstimadaRecepcion: moment(SolicitudesUpdated[i].fechaEstimadaRecepcion).format('YYYY-MM-DD'),
                fechaColocacion: moment(SolicitudesUpdated[i].fechaColocacion).format('YYYY-MM-DD')
            }
            state.Solicitudes.push(element)
        }
    },
    setLineasSolicitud(state, lineas){
        state.LineasSolicitudSelected = lineas

    }
}
const actions = {
    updateSolicitudes({
        commit
    }, solicitudes) {
        commit('updateSolicitudesMutation', solicitudes)
    },
    updateSolicitud({ commit }, nuevasolicitud) {
        commit('UpdateSolicitudMutation', nuevasolicitud)
    },
    updateProveedor({
        commit
    }, proveedorModel) {
        commit('updateProveedorMutation', proveedorModel)
    },
    async getLineasSolicitud({commit,state}) {
        var url = `SolicitudCompraLineas/solicitud/${state.SolicitudSelected.id}`;
        await axios
            .get(url)
            .then(resp => {
                commit('setLineasSolicitud',resp.data)
            })
            .catch(err => console.log(err));
    }
}

export default {
    state,
    mutations,
    actions,
    getters,
    namespaced: true,

}